.microphone-logo {
  border-radius: 50%;
  padding: 5%;
  background-color: white;
  width: 20%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.microphone-logo:active {
  -webkit-animation: pulse 1s infinite ease-in-out alternate;
          animation: pulse 1s infinite ease-in-out alternate;
}
@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  to {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}
@keyframes pulse {
  from {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  to {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}



