.button {
  margin-top: 10%;
  padding: 5px 50px 5px 50px;
  background-color: white;
  color: #474d4d;
  border: 4px solid #474d4d;
  font-size: 28px;
  font-family: 'Overpass';
  border-radius: 30px;
  font-weight: bold;
}
.button a {
  text-decoration: none;
  color: #474d4d;
}



